@import url('https://fonts.googleapis.com/css2?family=Sriracha&display=swap');

* {
	font-family: 'Sriracha', cursive;
	margin: 0;
}

.flex {
	display: flex;
	justify-content: center;
	align-items: center;
}

.btn {
	font-size: 25px;
	margin: 0 15px;
	cursor: pointer;
}

.btn.disabled {
	cursor: default;
}
